






























import { Component, Vue } from 'vue-property-decorator';
import AnalogClock from './AnalogClock.vue';

const API = 'https://angelm340.ddns.net/sound/';

@Component({
  components: {
    AnalogClock,
  },
})
export default class MainDisplay extends Vue {
  alarmaLoading = false;

  stopLoading = false;

  startAllowed = false;

  stopAllowed = false;

  iniciarAlarma() {
    this.alarmaLoading = true;

    fetch(`${API}?start`)
      .then(() => {
        this.alarmaLoading = false;
        this.getStatus();
      });
  }

  pararAlarma() {
    this.stopLoading = true;

    fetch(`${API}?stop`)
      .then(() => {
        this.stopLoading = false;
        this.getStatus();
      });
  }

  getStatus() {
    fetch(`${API}?status`)
      .then((resp) => resp.json()).then((apiStatus) => {
        const { status } = apiStatus;

        if (status === 'SOUNDING') {
          this.startAllowed = false;
          this.stopAllowed = true;
        } else if (status === 'STOPPED') {
          this.startAllowed = true;
          this.stopAllowed = false;
        }
      });
  }

  mounted() {
    this.getStatus();
  }
}
